import React, { useState,useContext } from 'react'
import styled from 'styled-components';
import {  Grid, Button,  Text } from '../../../style/components';
import { primaryColor } from '../../../config';
import { PanelItem, OptionPanel } from './panel';
import panelJSON from './panel-json';

interface PanelEditor {
    currentPanels: PanelItem[];
    setCurrentPanels: any;
    options: PanelItem[];
    close: any;
  }

  
const AddPanel = ({ currentPanels, setCurrentPanels, options, close }: PanelEditor) => {
    const NUM_PANELS = 5;
    const [panels, setPanels] = useState<PanelItem[]>([]);
    const remainingPanels = NUM_PANELS - currentPanels.length - panels.length;
    const select = (value: PanelItem) => setPanels([...panels, value]);
  
    const unselect = (value: PanelItem) => setPanels(panels.filter((el: PanelItem) => el !== value))
  
    const confirm = () => {
      setCurrentPanels([...currentPanels, ...panels]);
      close();
    }
  
    return (<div>
      <Text bold color={primaryColor} textAlign={"center"}>Select {remainingPanels} more Panels</Text>
      <Grid cols={5}>
        {options
          .filter((item: PanelItem) => !currentPanels.includes(item))
          .map((item: PanelItem) => {
            const { value, name, bg } = panelJSON[item];
            return (
              <OptionPanel 
                onClick={() => panels.includes(item)? unselect(item): select(item)}
                name={name} 
                el={<Number className="grid-box bold" >10</Number>}
                value={value} 
                bg={bg} 
                selected={panels.includes(item)}
              />
            );
          })}
      </Grid>
      <Button shadow style={{fontSize: ".76em"}} onClick={() => confirm()}>Confirm</Button>
      <Button shadow style={{fontSize: ".76em"}} onClick={() => {
        setPanels(currentPanels)
        close()
      }}>Cancel</Button>
     </div>)
  }
  

  const Number = styled.div`
    font-size: 60px;
    color: white;`;

    export default AddPanel;