import styled from 'styled-components';
import React from 'react';
import { primaryColor } from '../../config';
import { elementProps } from '../generator';

export const MenuItem = (props: any) => {
    const MI = styled.span`
    cursor : pointer;
    padding: .3em .5em;
    text-transform: capitalize;
    transition: 1000ms ease-out;
    color: ${primaryColor}
    border-radius: 1.5em; 
    font-weight: bold;
    ${props.active && `
    border-radius: .6em;
    box-shadow: 0 .13em .2em rgba(0,0,0,.5);
    background: ${props.inverted? "white" : primaryColor }; color: ${props.inverted? primaryColor: "white"}`}`;
    return (<MI {...elementProps(props)}>{props.children}</MI>);
}
