import React, { useContext, useEffect } from 'react';
import Sidebar from './style/components/sidebar';
import { NavContext } from './context/nav-context';
import styled from 'styled-components';
import { mobileWidth } from './config';

const Layout = ({ children, style }: any) => {
    const { sidebar } = useContext(NavContext);

    const Wrapper = styled.div<{bg : string | any}>`
        margin: auto;
        min-height: 100vh;
        margin-left: 10em;
        display: grid;
        place-items: start center;
        background: url(${props => props.bg});
        transition: all 0.5s ease-out;
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-position:  bottom left;
        @media(max-width: ${mobileWidth}){}`;
  useEffect(()=>
  window.scrollTo(0,0),[])
    return(<>
            <div>
            {sidebar &&  <Sidebar/>}
            </div>
            <Wrapper bg={""} style={style}>    
                {children}
            </Wrapper>
        </>
    );
}; 

export default Layout;