import React from 'react';
import { Analytics } from './pages';
import Providers from './providers'
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import TopNavBar from './style/components/topnav';
import './style/main.scss';
import 'antd/dist/antd.css';
import ErrorBoundary from 'antd/lib/alert/ErrorBoundary';
import Annoucement from './pages/announcement';
import Citations from './pages/citations';
import Events from './pages/events';
import Profile from './pages/profile';
import Settings from './pages/settings';

function App() {
  return (
    <ErrorBoundary>
      <Providers>
    <div 
      className="App" 
      style={{position: "absolute", top: 0, left: 0, padding: 0, margin: 0, paddingTop: "4.5em", background: "#EEF3F3", minWidth: "100%", minHeight: "100vh", boxSizing: "border-box"}}>
      <Router>
        <TopNavBar />
        <Switch>
         
          <Route path="/announcements" render={() => <Annoucement />} />
          <Route path="/events" render={() => <Events />} />
          <Route path="/citations" render={() => <Citations />} />
          <Route path="/profile" render={() => <Profile />} />
          <Route path="/settings" render={() => <Settings />} />

          <Route path="/" render={() => <Analytics />} />
        </Switch>
      </Router>
    </div>
    </Providers>
    </ErrorBoundary>
  );
}

export default App;
