import React, { useState, useContext, useEffect } from 'react';
import { Grid, Button } from '../../style/components';
import '../../style/main.scss'
import 'antd/dist/antd.css';
import { createPoints } from './util/data-generator';
import PanelList from './panel/panel-list';
import GraphWithFilter from './graph/graphWithFilter';
import { LineChart, InfoPanel } from '../../types';
import { getComponents } from './util/component-builder';
import { primaryColor } from '../../config';
import moment from 'moment';
import Layout from '../../layout';
import { ComponentsContext } from '../../context/components';
import GraphAdder from './graph/graph-adder';
import { message } from 'antd';

type VisualComponents = LineChart | InfoPanel;
const Analytics = () => {
  const data = createPoints(moment(new Date("2010-1-1"), 'YYYY-MM-DD'), moment(new Date("2010-1-1"), 'YYYY-MM-DD'));

  const loadComponents = () : VisualComponents[]=>{
    return JSON.parse(localStorage.getItem("graph-components") || "[]"); 
   }

  const [components, setComponents] = useState<VisualComponents[]>(loadComponents());
  const { editMode, toggleEditMode } = useContext(ComponentsContext);
  const KEY = "graph-components";
 
  const saveComponents = (components : VisualComponents[])=>{
    localStorage.setItem(KEY, JSON.stringify(components))
  }

  const remove = (index : number)=>{
    setComponents(components.filter((e : VisualComponents, i : number)=> i !== index));
    message.success('Your Graph was deleted');
  }

  useEffect(()=>{
    saveComponents(components)
  }, [ components])
  return (
    <Layout>
      <Grid style={{ maxWidth: "90vw", minHeight: "300px", minWidth: "40vw" }}>
        <div>
          <Button className="right" rounded shadow onClick={() => toggleEditMode()} minWidth="4.5em">
          <i className={`fa fa-${!editMode? 'edit' : 'check'}`} ></i>
          </Button>
          <PanelList />
          <Grid cols="2">
            <GraphWithFilter dataPoints={data} createPoints={createPoints} name={"Graph"} background={primaryColor}/>
            <GraphWithFilter createPoints={createPoints} name={"Graph"} background={primaryColor} />
            {components.map((item: VisualComponents, i : number) => getComponents(item, ()=>remove(i)))}
            <GraphAdder add={(el: VisualComponents) => { setComponents([...components, el]) }} />
          </Grid>
        </div>
      </Grid>
    </Layout>
  );
}


export default Analytics;
