import React from 'react';
import { Text, Button, Card } from '../../../style/components';
import { DatePicker, Select } from 'antd';
import { primaryColor } from '../../../config';

export const FilterBox = ({ setEndDate, range, setStartDate } : any)=>(
    <Card
      shadow
      style={{
      display: "grid", 
      height: "100%", 
      background: primaryColor,
      gridAutoColumns: "auto auto auto auto 1fr", 
      padding: ".5em 1em .5em 1.5em",
      placeItems: "end start"}}
    >
      <Text width="100%" color="white" textAlign="center" size={2} bold>Filter</Text>
      <Text color="white" textAlign="left" bold>Select Garage</Text>
      
      <Select
        disabled
        defaultValue={"Garage A"}
        style={{ borderRadius: 10, width: "100%", placeSelf: "end" }}
      />
      <Text color="white" textAlign="left" margin=".5em 0 0 0" bold>Start Date</Text>
  
      <DatePicker
        style={{ borderRadius: 8}}
        onChange={(e: any) => setStartDate(e)}
        value={range.startDate}
      />
     <Text color="white" bold margin=".5em 0 0 0">End Date</Text>
      <DatePicker
        style={{ borderRadius: 8}}
        onChange={(e: any) => setEndDate(e)}
        value={range.endDate}
      />
      <Button shadow bg="teal" margin=".8em 0 0 0" style={{placeSelf: "end stretch"}}>Generate Data</Button>
    </Card> 
  )