import React from 'react';
import { LineChart, InfoPanel, Bargraph } from '../../../types';
import GraphWithFilter from '../graph/graphWithFilter';
import { createPoints } from './data-generator';


export const getComponents = (El: LineChart | InfoPanel | Bargraph, remove : any) =>{

    switch(El.type){
        case "info-panel":
        return <div>InfoPanel:  {JSON.stringify(El)}</div>
        
        case "line-chart":   
        return  <GraphWithFilter createPoints={createPoints} {...El} editable={true} remove={remove} />
        
        default:
    return<div>Error <br /> Component: {JSON.stringify(El)}</div>
    }
}