import React, { useState, useEffect } from 'react';
import { Grid, Text } from '../../../style/components';
import { Card, Button, Modal } from '../../../style/components';
import { LineChart, InfoPanel, ElTypes } from '../../../types';
import { OptionPanel } from '../panel/panel';
import { Input, Select,  DatePicker, Progress, message } from 'antd';
import { primaryColor } from '../../../config';

type VisualComponents = LineChart | InfoPanel;

const GraphAdder = ({ add }: { add: (el: VisualComponents) => void }) => {
    const AdderModal = Modal({
        title: "Create Your Component",
        hideCancel: true,
        message: <MultiStep close={() => AdderModal.close()} add={add} />
    })

    return (<Card bgColor="lightgray" width="100%" grid shadow={false} placeSelf="start">
        <Button
            rounded
            minWidth="1.5em"
            minHeight="1.5em"
            onClick={() => AdderModal.open()}>
            <i className="fa fa-plus"></i>
        </Button>
    </Card>)
}


const ChartSelector = ({ setType, type }: { setType: (el: ElTypes) => void, type: ElTypes | "" }) => {
    return (
        <Grid customCols="auto auto">
            <OptionPanel
                bg="linear-gradient(-30deg, purple 10%, rgba(30, 203, 180, 0.7) 100%)"
                el={<img style={{ maxHeight: "150px" }} src={"https://ddgobkiprc33d.cloudfront.net/e4fbba81-9062-490d-90e4-0083314047ee.JPG"} />}
                onClick={() => setType("line-chart")}
                selected={"line-chart" === type}
                name="line-chart"
            />
            <OptionPanel
                bg="linear-gradient(-30deg, #1ecb7d 10%, rgba(30, 203, 180, 0.7) 100%)"
                el={<img style={{ maxHeight: "150px" }} src={"https://ddgobkiprc33d.cloudfront.net/e4fbba81-9062-490d-90e4-0083314047ee.JPG"} />}
                onClick={() => setType("bar-graph")}
                name="bar-graph"
                selected={"bar-graph" === type}
            />
        </Grid>
    )
}

const BackgroundSelector = ({ setBackground, background }: { setBackground: (el: string) => void, background: string }) => {
   const colors = [
        {color : primaryColor, name: "green"},
        {color : "coral", name: "orange"},
        {color : "#E11919", name: "red"},
        {color : "purple", name: "purple"},
        {color : "dodgerblue", name: "blue"},
    ]
    return (
        <Grid customCols="auto auto auto auto">
            {colors
            .map(({ color, name } : {color: string, name: string})=><OptionPanel
                                        bg={color}
                                        onClick={() => setBackground(color)}
                                        selected={color === background}
                                        name={name.charAt(0).toUpperCase()+ name.substring(1)}
                                />)}
        </Grid>
    )
}
const panelJSON = {
    "activeMembers": {
      bg: `linear-gradient(-50deg, #921ecb 0%, rgba(140, 95, 163, 0.86) 100%)`,
      api: ``,
      name: 'activeMembers',
      value: 10
    },
    "currentOccupancy": {
      bg: `linear-gradient(-50deg, #FF1eDb 0%, rgba(140, 125, 163, 0.86) 100%)`,
      api: ``,
      name: 'currentOccupancy',
      value: 10
    }
  };
const NameSelector = ({ name, setName }: any) => {

    return (
        <div>
            <div className="txt-shadow">Pick a name for your Graph:</div>
            <Input onChange={(e) => setName(e.target.value)} />

            <div className="txt-shadow">Pick the Days of the week</div>
            <Select defaultValue="All Days" disabled >
            </Select>

            <div className="txt-shadow">Pick the Days of the week</div>
            <DatePicker.RangePicker  disabled/>
        </div>
    )
}

const Confirmation = ({ finish }: any) => {
    const [count, setCount] = useState(0);
    useEffect(() => {
        const interval = setInterval(() => {
            setCount(count => count + 5);
        }, 100);
     
        return () => {
            clearInterval(interval);
        };
    }, []);

    useEffect(()=>  {
        if(count >= 100){
        finish();
        message.success('Your Changes was saved');
        }
    }, [count])
    return (
        <Grid width={"100%"}>
            <Text bold>
                {count < 30? "Creating " :  "Setting up "}
                your Chart</Text>
            <Progress percent={count} style={{ width: 400 }} />
        </Grid>
    )
}

const MultiStep = ({ add, close }: any) => {
    const [state, setState] = useState({
        step: 0,
        completed: false
    });

    const [chart, setChart] = useState<VisualComponents | any>({
        name: "",
        type: "",
        background: "",
        days: [],

    });
    const setType = (type: ElTypes) => {
        setChart({ ...chart, type });
        setState({ ...state, completed: true });
    }

    const setName = (name: string) => {
        setChart({ ...chart, name });
        setState({ ...state, completed: true });
    }

    const setBackground = (background: string) => {
        setChart({ ...chart, background });
        setState({ ...state, completed: true })
    }

    const forward = () => {
        if (state.step < InputComponents.length) {
            setState({ step: state.step + 1, completed: false });
        }
    }

    const finish = () => {
        add(chart);
        close();
    }

    const backward = () => {
        if (state.step > 0) {
            setState({ ...state, step: state.step - 1 })
        }
    }

    const InputComponents = [
        <ChartSelector setType={setType} type={chart.type} />,
        <BackgroundSelector setBackground={setBackground} background={chart.background} />,
        <NameSelector name={chart.name} setName={setName} />,
        <Confirmation finish={finish} />]
    return <> <div>
        {InputComponents[state.step]}
    </div>
        <Grid width={"100%"} placeItems="end">
            <Button onClick={forward} shadow disabled={!state.completed}>Next</Button>
        </Grid></>;
}

export default GraphAdder;