import React, { useRef, useEffect, useState, useContext } from 'react';
import VX from './graph';
import { Card, Header } from '../../../style/components';
import moment, { Moment } from 'moment';
import { FilterBox } from './filter';
import { Dropdown } from 'antd';
import { MenuItem } from '../../../style/components/menu';
import { ComponentsContext } from '../../../context/components';
import { primaryColor } from '../../../config';

const GraphWithFilter = ({ createPoints, name, background, dataPoints: defaultDataPoints, editable, remove }: any) => {
  const [el, setter] = useState({ x: 0, y: 0 });
  const container = useRef<any>(null);
  const { editMode } = useContext(ComponentsContext);
  const [range, setRange] = useState<any>({
    startDate: moment("2010-2-2", 'YYYY-MM-DD'),
    endDate: moment("2010-3-2", 'YYYY-MM-DD')
  })
  const [dataPoints, setDataPoints] = useState(defaultDataPoints || createPoints(range.startDate, range.endDate));
  const setEndDate = (date: Moment) => {
    if (date < range.startDate) {
      setRange({
        startDate: date,
        endDate: range.startDate
      })
    } else setRange({ ...range, endDate: date })
  }

  const setStartDate = (date: Moment) => {
    if (date > range.endDate) {
      setRange({
        startDate: range.endDate,
        endDate: date
      })
    } else setRange({ ...range, startDate: date })
  }

  useEffect(() => setDataPoints(createPoints(range.startDate, range.endDate)), [range]);
  const resize = () => {
    const { clientWidth: x, clientHeight: y } = container.current;
    setter({ x, y });
  }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => resize, [container?.current?.clientWidth]);

  useEffect(() => {
    window?.addEventListener("resize", resize);
    return () => window.removeEventListener("resize", resize);
  }, []);

  return (
    <Card placeSelf="stretch" shadow grid customCols={"1fr auto"} bgColor={background} gridGap={"0 1em"} className="slide-up">
      {<Header style={{ gridColumn: "1/-1", display: "grid", width: "100%", gridTemplateColumns: "1fr auto auto", placeItems: "start" }}
        color="white"
        bold
        size={4}>

        <div style={{ fontSize: "1.4em", lineHeight: ".8em" }}>{name}</div>
        {!defaultDataPoints && <>
          <div style={{ padding: "0 .5em" }}>
            <MenuItem active inverted>1D</MenuItem>
            <MenuItem inverted>1W</MenuItem>
            <MenuItem inverted>1M</MenuItem>
            <MenuItem inverted>3M</MenuItem>
            <MenuItem inverted>1Y</MenuItem>
          </div>

          {(!editMode) &&
            <Dropdown placement="bottomRight" overlay={<FilterBox range={range} setEndDate={setEndDate} setStartDate={setStartDate} />}>
              <a className="ant-dropdown-link rounded shadow"
                style={{ color: primaryColor, background: "white" }}
                onClick={e => e.preventDefault()}>
                <i className="fa fa-filter"></i>
              </a>
            </Dropdown>}
        </>}

        {editMode &&
          <a
            className="ant-dropdown-link rounded shadow"
            style={{ color: "white", background: editable? "red": "gray" }}
            onClick={e => e.preventDefault()}>
            <i className="fa fa-trash" onClick={()=>editable? remove() : {}}></i>
          </a>}
      </Header>}
      <div style={{ minWidth: "35vw", width: "100%" }} ref={container} >
        <VX width={el.x} height={300} dataPoints={dataPoints} background={background} />
      </div>
    </Card>
  );
}

export default GraphWithFilter;
