import React, { useState, useEffect } from 'react';
import Layout from '../../layout';
import { Card, Grid, Button, Modal, Text } from '../../style/components';
import { Tag, Space, Table, message, Input, DatePicker } from 'antd';
import { MenuItem, } from '../../style/components/menu';
import { primaryColor } from '../../config';
import img from '../../assets/announ.svg';
import { useFormik } from 'formik';
import { Moment } from 'moment';
import { Badge } from '../../style/components/badge';
import * as Yup from 'yup';

const columns = [{
    title: 'Title',
    dataIndex: 'title',
    key: 'title',
    render: (text: string) => <a>{text}</a>,
},
{
    title: 'Message',
    dataIndex: 'message',
    key: 'message',
},
{
    title: 'Status',
    dataIndex: 'status',
    render: (text: string) => <Badge className="shadow txt-center" style={{background: text === states.EXPIRED? "red": ""}}>{text}</Badge>,
},
{
    title: 'Launch Date',
    key: 'launch_date',
    dataIndex: 'launch_date',
},
{
    title: 'Expiry Date',
    key: 'expiry_date',
    dataIndex: 'expiry_date',
},
{
    title: 'Action',
    key: 'action',
    render: (text: any, record: any) => (
        <Space size="middle">
            <Button minWidth="3em"  disabled onClick={()=>{}}> <i className="fa fa-edit"></i> &nbsp;Edit</Button>
            <Button   disabled  minWidth="3em"><i className="fa fa-trash"></i> &nbsp;Delete</Button>
        </Space>
    ),
},
];

const data = [{
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "ACTIVE",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    },
    {
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "ACTIVE",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    },  {
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "EXPIRED",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    }, {
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "EXPIRED",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    },
    {
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "ACTIVE",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    }
    ,{
        title: 'SJSU Graduation Ceramony 2020 Fall',
        message: 'A Long Message',
        status: "EXPIRED",
        launch_date: '2020-6-22',
        expiry_date: "2020-6-22",
    }
];
const states = {
    "ACTIVE": "ACTIVE",
    "ALL": "ALL",
    "EXPIRED": "EXPIRED",
    "DEFAULT": "ALL",
    "states": ["ACTIVE", "ALL", "EXPIRED"]
}

interface Announcement{
    title: string;
    message: string;
    status: string;
    created: string;
    last_modified: string;
}

const Annoucement = () => {
    const KEY = "announcements";

  const loadAnnouncements = () : Announcement[]=>{
    return JSON.parse(localStorage.getItem(KEY) || JSON.stringify(data)); 
   }
   const addAnnouncement = (announcement : Announcement)=>{
    setState({...state, announcements: [...state.announcements, announcement]});
    message.success('Your Annoucement was added');
}
   const AddModal = Modal({
       title: "Add Your Event",
       message: <Adder add={addAnnouncement} close={()=>AddModal.close()}/>,
       hideCancel: true
   })

    const [state, setState] = useState({
        active: states.DEFAULT,
        loading: true,
        announcements: loadAnnouncements()
    });

    const saveAnnouncements = (announcements : Announcement[])=>{
      localStorage.setItem(KEY, JSON.stringify(announcements))
    }
  
    const removeAnnouncement = (index : number)=>{
      setState({...state, announcements : state.announcements.filter((e : Announcement, i : number)=> i !== index)});
      message.success('Your Annoucement was deleted');
    }

   const currentAnnouncements = state.announcements.filter(({ status}: any)=>status === state.active || state.active === states.ALL) 
   useEffect(()=>{
        saveAnnouncements(state.announcements)
      }, [state.announcements])
    
    return (
        <Layout>
            <Grid gridGap="0">
            <Button style={{placeSelf: "end"}} rounded shadow onClick={() => AddModal.open()} minWidth="4.5em">
                <i className={`fa fa-${"plus"}`} ></i> Add
            </Button>
            <Card shadow minWidth="60vw">
                <Grid customCols="1fr auto 1fr" padding=".5em">
                    <div></div>
                    <div className="shadow" style={{background: "#CDF7E0", padding: ".2em", borderRadius: "1.5em"}}>
                    {states.states
                        .map((active: string) => <MenuItem
                            style={{borderRadius: "1.5em"}}
                            active={state.active === active}
                            onClick={() => setState({ ...state, active })}>
                            {active.charAt(0) + active.substring(1).toLowerCase()}
                        </MenuItem>)}
                    <div></div>
                    </div>
                </Grid>
                <Table style={{color: primaryColor}} dataSource={currentAnnouncements} columns={columns} />
            </Card>
            </Grid>
        </Layout>
    );
}

const Adder = (props : any)=>{
    return(<Grid customCols="3fr 2fr" width="100%" height="100%">
        <div style={{background: "white", height: "100%"}}>
        <img src={img} style={{maxWidth: "100%"}}/>
        </div>
        <Form {...props}/>
    </Grid>)
}

const validationSchema = Yup.object({
    title : Yup.string().required("Required").min(1,""),
    message: Yup.string().required("Required!"),
    launch_date: Yup.string().required("Required!").min(1,""),
    expiry_date: Yup.string().required("Required!").min(1,""),
  });


const Form = ({ add , close} : any)=>{
    const { handleSubmit, handleChange, handleBlur,  errors, values, setFieldValue, isValid } = useFormik({
        initialValues: { title: "", message: "", status: "ACTIVE", launch_date: "", expiry_date: "" },
        onSubmit(values) {
            submit(values) 
          },
         
          validationSchema

        })
    const submit = (values: any)=>{
        add(values);
        close();
    }

    return(   
        <form onSubmit={handleSubmit} style={{padding: ".5em 1em", display: "grid", height: "100%", width: "100%", placeItems: "center stretch", gridGap: ".4em"}}>
        <div>
            <Text textAlign="left"  bold>Annoucement Title</Text>
            <Input  onBlur={handleBlur} name={"title"} value={values.title} onChange={handleChange} placeholder="Example: SJSU Graduation Ceramony"/>
        </div>

        <div>
            <Text textAlign="left"  bold>Annoucement Message</Text>
            <Input  onBlur={handleBlur}  name={"message"} value={values.message}  onChange={handleChange}  placeholder="Example: Information on the Event"/>
        </div>
       
        <div style={{display: "grid", placeItems: "end"}}>
            <Text textAlign="left"  placeSelf="stretch"  bold>Message Launch Date</Text>
            <DatePicker 
             showTime 
             minuteStep={15} 
             onChange={(e: Moment | null, date: string)=> setFieldValue("launch_date", date)}/>
        </div>

        <div style={{display: "grid", placeItems: "end"}}>
            <Text textAlign="left"  placeSelf="stretch"  bold>Message Expiry Date</Text>
            <DatePicker  
            showTime 
            minuteStep={15} 
            onChange={(e: Moment | null, date: string)=> setFieldValue("expiry_date", date)}/>
        </div>

        <Grid cols="2">
            <Button shadow invert minWidth="100%" onClick={close} >Cancel</Button>
            <Button shadow minWidth="100%" disable={!values.expiry_date ||! values.launch_date || !values.message || !values.title} onClick={handleSubmit}>Confirm</Button>
        </Grid>
    </form>)
}



export default Annoucement;