import React from 'react'
import styled from 'styled-components';
import { Card, } from '../../../style/components';
import CountUp from 'react-countup';
import {  Radio } from 'antd';

const intoHeadings = (str: string): string => {
  if (!intoHeadings || intoHeadings.length === 0) throw new Error("");
  return str.charAt(0).toUpperCase() + str.substring(1).replace(/([a-z](?=[A-Z]))/g, '$1 ')
}

export type PanelItem = 'activeGarages' | 'activeAnnouncement' | 'activeEvent' | 'activeMembers' | 'currentOccupancy';

export const InfoPanel = ({ name, value, bg , editMode, remove, index}: any) => (
  <Card
    shadow
    hover
    className="noselect"
    color="white"
    style={{ backgroundImage: bg, cursor: "pointer", backgroundSize: "cover" }}
  >
    <div className="txt-center bold" style={{ fontSize: ".9em", color: "white", fontWeight: "bold"}}>
      <span style={{textShadow: "1px 1px gray" }} dangerouslySetInnerHTML={{ __html: String(intoHeadings(name)) }} />
      {editMode && <i className="fa fa-trash right txt-shadow txt-md" onClick={()=>remove(index)}></i>}
    </div> 
 

    <Number className="grid-box bold" >
      <CountUp duration={2} start={0} end={value} />
    </Number>
  </Card>
);

export const OptionPanel = ({ name, value, bg, selected , onClick, el}: any) => (
  <Card
    onClick={onClick}
    hover
    shadow
    grid
    color="white"
    style={{ background: bg, cursor: "pointer", backgroundSize: "cover" }}
  >
    <div className="txt-center bold" style={{ fontSize: ".9em", color: "white" }}>
    <Radio.Group value={selected? 1: 0}>
      <Radio value={1}></Radio>
    </Radio.Group>
      <span dangerouslySetInnerHTML={{ __html: String(intoHeadings(name)) }} />
    </div>
      {el}
  </Card>
);

const Number = styled.div`
  font-size: 60px;
  color: white;`;
