import React, { useState,useContext } from 'react'
import { Card, Grid, Button, Modal, Text } from '../../../style/components';
import { ComponentsContext } from '../../../context/components';
import { InfoPanel, PanelItem } from './panel';
import AddPanel from './add-panel';
import panelJSON from './panel-json';

const intoHeadings = (str: string): string => {
  if (!intoHeadings || intoHeadings.length === 0) throw new Error("");
  return str.charAt(0).toUpperCase() + str.substring(1).replace(/([a-z](?=[A-Z]))/g, '$1 ')
}

const PanelList = ({ style }: any) => {
  const [currentPanels, setCurrentPanels] = useState<PanelItem[]>(['activeGarages', 'activeAnnouncement', 'activeEvent'])
  const { editMode } = useContext(ComponentsContext);
  const closeAddModal = () => addModal?.close();

  const addModal = Modal({
    title: "Add",
    hideCancel: true,
    message: <AddPanel
      currentPanels={currentPanels}
      close={closeAddModal}
      setCurrentPanels={setCurrentPanels}
      options={['activeGarages', 'activeAnnouncement', 'activeEvent', 'activeMembers', 'currentOccupancy']}
    />
  });
 
  const remove = (index : number)=>{
    setCurrentPanels(currentPanels.filter((item : PanelItem, i: number) => i != index))
  }

  return (
    <div className="slide-up">
      <Grid style={style} customCols="repeat(5, 1fr)" gridGap="0 1em" width="100%" placeItems="stretch" mobileCols={"1fr auto"}>
        {currentPanels.map((panel: PanelItem, index : number) => {
          const { value, name, bg } = panelJSON[panel];
          return (<InfoPanel name={name} value={value} bg={bg} editMode={editMode} remove={remove} index={index}/>);
        })}
        {currentPanels.length < 5 && <Card bgColor="lightgray" grid shadow={false}>
          <Button rounded minWidth="1.5em" minHeight="1.5em" onClick={() => { addModal.open() }}>
            <i className="fa fa-plus"></i>
          </Button>
        </Card>}
      </Grid>
    </div>);
}

export default PanelList;